    .bg-info {
      margin: 0px;
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
      background-size: cover;
      background-attachment: fixed;
      padding: 15vh 20vw;
      
        
      .inside-card {
        display: grid;
        grid-template-rows: 50px auto 50px / minmax(0, 1fr);
        background-color: white;
        padding: 10vh 5vw;
        gap: 5vw;

        > button {
          justify-self: center;
          width: 20%;
          cursor: pointer;
          border: 2px solid var(--dark-blue);
          background-color: #2ec4b6;
          font-weight: 700;
          font-size: 1rem;
          color: white;
          padding: 8px 25px;
          border-radius: 10px;

        }

        h1 {
          margin: 0px;
          font-family: 'MyFont';
          font-size: 4rem;
          text-align: center;
          color: black;
        }

        .card-list {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
          grid-column-gap: 20px;
          grid-row-gap: 50px;

          .card {
            color: white;
            height: 150px;
            display: grid;
            grid-template-columns: auto 1fr;
            background-color: var(--white);
            box-shadow: 0px 0px 10px 5px var(--grey);

            img {
              height: 150px;
            }

            div {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              height: 100%;
              padding: 5%;
              font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

              h3 {
                margin: 0px;

                display: flex;
                justify-content: space-between;
                color: black;
                font-size: 1.2rem;

                span {
                  text-align: center;
                  width: 45px;
                  height: 20px;
                  background-color: var(--dark-blue);
                  font-size: 1rem;
                  padding: 0px 5px;
                  color: white;
                }

              }

              p {
                font-size: 0.8rem;
                color: black;
              }

              > button {
                justify-self: center;
                cursor: pointer;
                border: 2px solid var(--dark-blue);
                background-color: #2ec4b6;
                font-size: 0.9rem;
                color: white;
                padding: 3px 7px;
                width: 30%;
                border-radius: 10px;

              }
            }
          }


        }
      }
    }

